* {
    box-sizing: border-box;
}

body{
    margin : 0;
    padding : 0;
    
    background-color: #eff3f7;
    height : 100%
}

html,
body,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    /* padding: 50px;
    padding-top: 70px;
    width: 80%; */
}


.movies.movies{
    width: 35%;
    background-color: white;
    margin-bottom: 70px;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;
    color: #b2b3bc
}

.movies img {
    position: relative;
    top: -50px;
    max-width: 180px;
    margin-right : 30px;
}

.movies .movie_title, 
.movies .movie_year{
    margin: 0;
    font-weight: 300;

}

.movies .movie_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c
}

.movies .movie_year,
.movies .movie_genres {
    color: #b2b3bc
}